// screens/LoginScreen.js
import React, { useEffect, useState } from "react";
import {
  View,
  Text,
  Image,
  TouchableOpacity,
  StyleSheet,
  Alert,
} from "react-native";
import * as Google from "expo-auth-session/providers/google";
import firebase from "firebase/compat/app";
import { auth } from "../firebase";
import Constants from "expo-constants";
import * as WebBrowser from "expo-web-browser";
import { loginWithGoogle } from "../utils/api"; //  
import * as SecureStore from "expo-secure-store";

WebBrowser.maybeCompleteAuthSession();

export default function LoginScreen({ navigation }) {
  const { iosClientId, webClientId } = Constants.expoConfig?.extra ?? {};
  const [isLoading, setIsLoading] = useState(false);
  const [request, response, promptAsync] = Google.useAuthRequest({
    iosClientId,
    webClientId,
  });

  useEffect(() => {
    if (response?.type === "success" && response.authentication?.idToken) {
      handleGoogleResponse(response.authentication);
    }
  }, [response]);

  const handleGoogleResponse = async ({ idToken: rawIdToken, accessToken }) => {
    setIsLoading(true);
    try {
      if (!rawIdToken) throw new Error("ID   .");

      // 1) Firebase 
      const credential = firebase.auth.GoogleAuthProvider.credential(
        rawIdToken,
        accessToken
      );
      const { user } = await auth.signInWithCredential(credential);

      // 2) Firebase ID 
      const firebaseIdToken = await user.getIdToken(true);
      console.log("id token", await user.getIdToken());

      // 3)   API 
      const res = await loginWithGoogle({ idToken: firebaseIdToken });
      const { accessToken: jwt, user: backendUser } = res.data;

      // 4) JWT 
      await SecureStore.setItemAsync("accessToken", jwt);
      console.log("JWT Token:", jwt);

      //  userId  
      await SecureStore.setItemAsync("userId", String(backendUser.id));
      // 5) 
      if (!backendUser.nickname) {
        navigation.replace("Name");
      } else if (!backendUser.profile_image) {
        navigation.replace("ProfileImage");
      } else {
        navigation.replace("Home");
      }
    } catch (error) {
      console.error("===   ===", error);
      if (error.response) {
        console.error("Response status:", error.response.status);
        console.error("Response data:", error.response.data);
      }
      Alert.alert(
        " ",
        error.response?.data?.message || error.message
      );
    } finally {
      setIsLoading(false);
    }
  };

  const handleGoogleLogin = async () => {
    if (!request) {
      Alert.alert(" ", "Google   .");
      return;
    }
    try {
      await auth.signOut();
      await promptAsync({ prompt: "select_account" });
    } catch (error) {
      console.error(" :", error);
      Alert.alert(" ", " .");
    }
  };

  return (
    <View style={styles.container}>
      <Image source={require("../assets/logoText.png")} style={styles.logo} />
      <Text style={styles.slogan}> </Text>
      <Image
        source={require("../assets/loginLogo.png")}
        style={styles.loginLogo}
      />

      {/* <TouchableOpacity
        style={styles.kakaoButton}
        onPress={() => navigation.navigate("Name")}
      >
        <Text style={styles.kakaoText}> </Text>
      </TouchableOpacity> */}

      <TouchableOpacity
        style={[styles.googleButton, isLoading && styles.disabledButton]}
        onPress={handleGoogleLogin}
        disabled={isLoading || !request}
      >
        <Text style={styles.googleText}>
          {isLoading ? " ..." : " "}
        </Text>
      </TouchableOpacity>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "center",
    // justifyContent: 'center',
    backgroundColor: "#fff",
  },
  slogan: { fontSize: 16 },
  kakaoButton: {
    backgroundColor: "#FEE500",
    width: "80%",
    padding: 12,
    borderRadius: 8,
    // marginTop: 24,
    alignItems: "center",
  },
  kakaoText: { fontWeight: "bold" },
  googleButton: {
    borderColor: "#ccc",
    borderWidth: 1,
    width: "80%",
    padding: 12,
    borderRadius: 8,
    marginTop: 17,
    alignItems: "center",
  },
  googleText: {},
  logo: {
    width: 206,
    height: 84,
    marginBottom: 35,
    marginTop: 202,
  },
  loginLogo: {
    width: 313,
    height: 228,
    marginTop: 61,
    marginBottom: 39,
  },
});
