import React, { useEffect, useState } from 'react';
import { View, Text, ScrollView, TouchableOpacity, Image } from 'react-native';
import { styles } from './styles/Group.styles';
import TodoList from '../components/TodoList';
import SubjectList from '../components/SubjectList';
import WeeklyStars from '../components/WeeklyStars';
import DividingLine from '../components/dividingLine';
import GroupMemberList from '../components/GroupMemberList';
import NameTag from '../components/NameTag';
import BottomNav from "../components/BottomNav";
import { useNavigation } from '@react-navigation/native';
import { fetchGroupsByUser, fetchGroupDetail, fetchTodosByGroup } from '../utils/api';
import { USER_ID } from "@env";

// TODO:   , todo , ,   

export default function GroupScreen() {
  const userId = USER_ID;
  const navigation = useNavigation();
  const [groups, setGroups] = useState([]);
  const [selectedGroupId, setSelectedGroupId] = useState(null);

  useEffect(() => {
    const loadGroups = async () => {
      try {
        const data = await fetchGroupsByUser(userId);
        setGroups(data);

        //      
        if (data.length > 0) {
          setSelectedGroupId(data[0].group_id);
        }
      } catch (error) {
        if (error.response?.status === 404) {
          console.warn("  :    ");
          setGroups([]); //   
        } else {
          console.error("   :", error);
        }
      }
    };

    loadGroups();
  }, []);

  const [groupDetail, setGroupDetail] = useState(null);
  const myNickname = groupDetail?.members?.find(m => m.uid === userId)?.nickname ?? '';

  //      
  useEffect(() => {
    const loadGroupDetail = async () => {
      if (!selectedGroupId) return;
      console.log("  group_id:", selectedGroupId);
      try {
        const data = await fetchGroupDetail(selectedGroupId);
        console.log("  groupDetail:", data);
        setGroupDetail(data);
      } catch (error) {
        console.error('     :', error);
      }
    };

    loadGroupDetail();
  }, [selectedGroupId]);

  const [todos, setTodos] = useState([]);

  useEffect(() => {
    const loadTodos = async () => {
      if (!selectedGroupId) return;

      try {
        const res = await fetchTodosByGroup(selectedGroupId);
        setTodos(res.data); //    
      } catch (error) {
        console.error("  :", error);
      }
    };

    loadTodos();
  }, [selectedGroupId]);

  const myTodos = todos.filter(todo => todo.user_id === userId);
  const otherTodosMap = {};

  todos.forEach(todo => {
    if (todo.user_id !== userId) {
      if (!otherTodosMap[todo.user_id]) {
        otherTodosMap[todo.user_id] = {
          user_id: todo.user_id,
          nickname: todo.user?.nickname ?? ' ',
          color: todo.user_color,
          todos: [],
        };
      }
      otherTodosMap[todo.user_id].todos.push(todo);
    }
  });

  const otherMembers = Object.values(otherTodosMap);

  const [now] = useState(new Date());
  const days = ['', '', '', '', '', '', ''];
  const formattedDate = `${now.getFullYear()} ${now.getMonth() + 1} ${now.getDate()} (${days[now.getDay()]})`;

  return (
    <View style={{ flex: 1, backgroundColor: "#fff" }}>
      <ScrollView style={{ backgroundColor: '#fff' }}>
        <SubjectList
          groups={groups}
          selectedGroupId={selectedGroupId}
          onSelectGroup={setSelectedGroupId}
        />
        <View style={styles.container}>
          <View style={styles.today}>
            <Text style={styles.text}>{formattedDate}</Text>
            <Text style={styles.text}>01:07:40</Text>
          </View>
          <WeeklyStars />
          <DividingLine />
          <View style={styles.info}>
            <TouchableOpacity
              style={[styles.Box, { gap: 9 }]}
              onPress={() => navigation.navigate('Rank')}
            >
              <Image source={require('../assets/.png')} style={styles.image} />
              <Text style={styles.rankText}>  ?</Text>
            </TouchableOpacity>
            <View style={[styles.Box, { gap: 14 }]}>
              <Text></Text>
              <Text style={styles.code}>
                {groupDetail?.secret_code ?? ' '}
              </Text>
            </View>
          </View>
        </View>
        <View style={styles.memberBox}>
          <Text style={styles.count}>
            {groupDetail?.attendance_count ?? 0}  {"\n"} !
          </Text>
          <GroupMemberList members={groupDetail?.members ?? []} />
        </View>
        <View style={styles.container}>
          <NameTag name={myNickname} />
          {myTodos.map(todo => (
            <TodoList
              key={todo.uid}
              title={todo.title}
              is_completed={todo.is_completed}
              memberColor={todo.user_color}
            />
          ))}

          <View style={styles.line} />

          {otherMembers.map(member => (
            <View key={member.user_id}>
              <NameTag name={member.nickname} showPlus={false} />
              {member.todos.map(todo => (
                <TodoList
                  key={todo.uid}
                  title={todo.title}
                  is_completed={todo.is_completed}
                  memberColor={member.color}
                />
              ))}
            </View>
          ))}
        </View>
      </ScrollView>
      <BottomNav />
    </View>
  );
}