import { TouchableOpacity, Text, StyleSheet } from "react-native";

export default function NextButton({ title, onPress, disabled, style }) {
  return (
    <TouchableOpacity
      style={[
        styles.button,
        style, //   
        { backgroundColor: disabled ? "#ABB0BC" : "#F57353" }, //    
      ]}
      disabled={disabled}
      onPress={onPress}
    >
      <Text style={styles.buttonText}>{title}</Text>
    </TouchableOpacity>
  );
}

const styles = StyleSheet.create({
  button: {
    width: 328,
    height: 61,
    borderRadius: 30,
    alignItems: "center",
    justifyContent: "center",
  },
  buttonText: {
    color: "#fff",
    textAlign: "center",
    fontSize: 20,
  },
});
