import React, { useEffect, useState } from 'react';
import { View, Image, Text, Modal, Button, Alert, TextInput, TouchableOpacity } from 'react-native';
import { styles } from './styles/Profile.styles';
import GlobalScreenWrapper from '../constants/GlobalScreenWrapper';
import BottomNav from "../components/BottomNav";
import InfoCard from '../components/InfoCard';
import EditIcon from '../assets/EditIcon.svg';
import TrashIcon from '../assets/TrashIcon.svg';
import AddIcon from '../assets/AddIcon.svg';
import { useNavigation } from '@react-navigation/native';
import { fetchUserInfo, updateUserInfo, fetchDdays, postDday, deleteDday, fetchGroupsByUser } from '../utils/api';
import { ScrollView } from 'react-native-gesture-handler';

export default function ProfileScreen() {
  const navigation = useNavigation();
  const [name, setName] = useState("");
  const [slogan, setSlogan] = useState("");
  const [profileImage, setProfileImage] = useState("");

  const attendance_count = 8;
  const heart = 38;

  useEffect(() => {
    const UserInfo = async () => {
      try {
        const user = await fetchUserInfo();
        setName(user.nickname);
        setSlogan(user.slogan ?? "  ",);
        setProfileImage(user.profile_image ?? "")
      } catch (err) {
        console.error("   :", err);
      }
    };

    UserInfo();
  }, []);

  const [ddayItems, setDdayItems] = useState([]);

  useEffect(() => {
    const getDdays = async () => {
      try {
        const ddays = await fetchDdays();
        setDdayItems(ddays);
      } catch (err) {
        console.log("  : ", err)
        setDdayItems([]);
      }
    }
    getDdays();
  }, [])

  const [groupTags, setGroupTags] = useState([]);
  const currentGroupCount = groupTags.length;

  useEffect(() => {
    const getGroups = async () => {
      try {
        const groups = await fetchGroupsByUser();
        setGroupTags(groups);
      } catch (err) {
        console.log("   : ", err)
        setGroupTags([]);
      }
    }
    getGroups();
  }, [])

  const [showProfileEdit, setShowProfileEdit] = useState(false);
  const [showGroupBottomSheet, setShowGroupBottomSheet] = useState(false);

  const handleProfileEdit = () => {
    setShowProfileEdit(true); //  
  };

  const [showDdayDeleteModal, setShowDdayDeleteModal] = useState(false);
  const [showDdayAddModal, setShowDdayAddModal] = useState(false);
  const [ddayTitle, setDdayTitle] = useState('');
  const [ddayDate, setDdayDate] = useState('');

  const calculateDdayText = (dateStr, title) => {
    const today = new Date();
    const target = new Date(dateStr);
    const diff = Math.ceil((target - today) / (1000 * 60 * 60 * 24)); //   

    let prefix = '';
    if (diff > 0) prefix = `D-${diff}`;
    else if (diff === 0) prefix = 'D-day';
    else prefix = `D+${Math.abs(diff)}`;

    return `${prefix}  ${title}`;
  };

  const handleAddGroup = () => {
    setShowGroupBottomSheet(true); //  
  };

  return (
    <View style={{ flex: 1, backgroundColor: "#fff" }}>
      <ScrollView>
        <GlobalScreenWrapper>
          <View style={styles.LogoBox}>
            <Image source={require('../assets/profileLogo.png')} style={styles.Logo} />
          </View>
          <View style={styles.profileBox}>
            <View style={styles.circle}>
              <Image
                source={profileImage
                  ? { uri: `data:image/png;base64,${profileImage}` }
                  : require('../assets/icon.png')}
                style={styles.image}
              />
            </View>
            <View style={styles.profile}>
              <Text style={styles.name}>{name}</Text>
              <Text style={styles.slogan}>{slogan}</Text>
            </View>
          </View>
          <View style={styles.reactionBox}>
            <View style={styles.reactions}>
              <Image source={require('../assets/.png')} style={styles.reactionImg} />
              <Text style={styles.reactionText}>{attendance_count}  </Text>
            </View>
            <View style={styles.reactions}>
              <Image source={require('../assets/.png')} style={styles.reactionImg} />
              <Text style={styles.reactionText}>  {heart}</Text>
            </View>
          </View>

          <View>
            <InfoCard title="" icon={<EditIcon />} onIconPress={handleProfileEdit}>
              <View style={styles.row}>
                <Text></Text>
                <Text>{name}</Text>
              </View>
              <View style={styles.row}>
                <Text></Text>
                <Text>{slogan}</Text>
              </View>
            </InfoCard>

            <InfoCard
              title="D-day"
              icon={<TrashIcon />}
              onIconPress={() => setShowDdayDeleteModal(true)}
            >
              {ddayItems.map((item) => (
                <Text key={item.uid}>{calculateDdayText(item.end_at, item.title)}  </Text>
              ))}
              <TouchableOpacity style={styles.addDday} onPress={() => setShowDdayAddModal(true)}>
                <AddIcon fill="#C8C8C8" />
                <Text style={{ color: '#c8c8c8' }}> </Text>
              </TouchableOpacity>
            </InfoCard>

            <InfoCard title=" " icon={<AddIcon fill="#f1f1f1" />} onIconPress={handleAddGroup} contentStyle={styles.tagContainer}>
              {groupTags.map((tag, idx) => (
                <Text key={idx} style={styles.tag}>{tag.name}</Text>
              ))}
            </InfoCard>

            {/*    */}
            <Modal visible={showProfileEdit} transparent animationType="slide">
              <View style={styles.modalContainer}>
                <View style={styles.modal}>
                  <Text style={styles.modalText}></Text>
                  <TextInput
                    value={name}
                    onChangeText={setName}
                    style={styles.input}
                  />
                  <Text style={styles.modalText}></Text>
                  <TextInput
                    value={slogan}
                    onChangeText={setSlogan}
                    style={styles.input}
                  />
                  <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                    <Button title="" onPress={() => setShowProfileEdit(false)} />
                    <Button
                      title=""
                      onPress={async () => {
                        try {
                          await updateUserInfo({ nickname: name, slogan });
                          Alert.alert("  .");
                          setShowProfileEdit(false);
                        } catch (err) {
                          console.error("  :", err);
                          Alert.alert("  ", "  .");
                        }
                      }}
                    />
                  </View>
                </View>
              </View>
            </Modal>

            {/*    */}
            <Modal visible={showDdayDeleteModal} transparent animationType="fade">
              <View style={styles.modalContainer}>
                <View style={styles.modal}>
                  <Text style={styles.modalText}> D-day </Text>

                  {/* {ddayItems && (
                    <Text style={{ textAlign: 'center', marginVertical: 10 }}>  .</Text>
                  )} */}

                  {ddayItems.map((item) => (
                    <View key={item.uid} style={styles.ddayRow}>
                      <Text style={{ flex: 1 }}>{item.title}</Text>
                      <Button
                        title=""
                        onPress={async () => {
                          try {
                            await deleteDday(item.uid);
                            setDdayItems(prev => prev.filter(dday => dday.uid !== item.uid)); //   
                          } catch (err) {
                            console.log("  : ", err)
                          }
                        }}
                      />
                    </View>
                  ))}

                  <Button title="" onPress={() => setShowDdayDeleteModal(false)} />
                </View>
              </View>
            </Modal>

            {/*    */}
            <Modal visible={showDdayAddModal} transparent animationType="slide">
              <View style={styles.modalContainer}>
                <View style={styles.modal}>
                  <Text style={styles.modalText}>D-day </Text>
                  <TextInput
                    placeholder=": "
                    value={ddayTitle}
                    onChangeText={setDdayTitle}
                    style={styles.input}
                  />

                  <Text style={styles.modalText}> (YYYY-MM-DD)</Text>
                  <TextInput
                    placeholder=": 2025-07-10"
                    value={ddayDate}
                    onChangeText={setDdayDate}
                    style={styles.input}
                    keyboardType="numbers-and-punctuation"
                  />

                  <View style={{ flexDirection: 'row', justifyContent: 'space-between' }}>
                    <Button title="" onPress={() => {
                      setDdayTitle('');
                      setDdayDate('');
                      setShowDdayAddModal(false);
                    }} />
                    <Button title="" onPress={async () => {
                      if (!ddayTitle.trim() || !ddayDate.trim()) return;

                      try {
                        await postDday({ title: ddayTitle, end_at: ddayDate });

                        const text = calculateDdayText(ddayDate, ddayTitle);
                        const newItem = {
                          id: Date.now(),
                          text
                        };

                        setDdayItems(prev => [...prev, newItem]);
                        setDdayTitle('');
                        setDdayDate('');
                        setShowDdayAddModal(false);
                      } catch (err) {
                        console.error("D-day  :", err);
                      }
                    }} />
                  </View>
                </View>
              </View>
            </Modal>


            {/*   */}
            <Modal visible={showGroupBottomSheet} transparent animationType="slide">
              <View style={styles.modalContainer}>
                <View style={styles.groupModal}>
                  <View style={styles.groupModalHeader}>
                    <Text style={styles.modalText}> </Text>
                    <Text style={styles.modalSubText}>   <Text style={styles.count}>{currentGroupCount}</Text></Text>
                  </View>

                  <TouchableOpacity
                    style={styles.groupCreateBtn}
                    onPress={() => {
                      setShowGroupBottomSheet(false);
                      navigation.navigate('GroupName');
                    }}
                  >
                    <Text style={styles.groupCreateBtnText}>   </Text>
                  </TouchableOpacity>

                  <TouchableOpacity
                    style={styles.groupJoinBtn}
                    onPress={() => {
                      setShowGroupBottomSheet(false);
                      navigation.navigate('GroupJoin1');
                    }}
                  >
                    <Text style={styles.groupJoinBtnText}>   </Text>
                  </TouchableOpacity>

                  <Button title="" onPress={() => setShowGroupBottomSheet(false)} />
                </View>
              </View>
            </Modal>

          </View>

        </GlobalScreenWrapper >
      </ScrollView>
      <BottomNav />
    </View >
  );
}