// screens/NameScreen.js
import React, { useState, useEffect } from "react";
import { View, Text, Image, StyleSheet, Alert } from "react-native";
import InputField from "../components/InputField";
import NextButton from "../components/NextButton";
import Header from "../components/Header";
import { registerNickname } from "../utils/api";   //  named export
import * as SecureStore from "expo-secure-store";

export default function NameScreen({ navigation }) {
  const [name, setName] = useState("");
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    (async () => {
      const token = await SecureStore.getItemAsync("accessToken");
      if (!token) {
        Alert.alert(" ", " .", [
          { text: "", onPress: () => navigation.replace("Login") }
        ]);
      }
    })();
  }, []);

  const handleNext = async () => {
    if (!name.trim()) {
      return Alert.alert("", " .");
    }
    setIsLoading(true);
    try {
      const res = await registerNickname(name);
      console.log("===    ===", res.data);
      navigation.replace("ProfileImage");
    } catch (err) {
      console.error("===    ===", err.response || err);
      if (err.response?.status === 401) {
        Alert.alert(" ", " .", [
          { text: "", onPress: () => navigation.replace("Login") }
        ]);
      } else {
        Alert.alert("", "  .");
      }
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <View style={styles.container}>
      <Header />
      <Text style={styles.title}>  step 1</Text>
      <Text style={styles.label}> !</Text>
      <Image
        source={require("../assets/character.png")}
        style={styles.image}
      />
      <InputField
        value={name}
        onChangeText={setName}
        placeholder=" "
      />
      <NextButton
        title={isLoading ? " ..." : " "}
        disabled={!name || isLoading}
        onPress={handleNext}
        style={styles.btn}
      />
    </View>
  );
}

const styles = StyleSheet.create({
  container: { flex: 1, padding: 31, backgroundColor: "#fff" },
  title: { marginVertical: 55, fontSize: 14, color: "#7A7A7A" },
  label: { fontSize: 16, fontWeight: "bold", marginBottom: 9 },
  btn: { marginTop: 312 },
  image: {
    position: "absolute",
    top: 140,
    left: 260,
    width: 96,
    height: 118,
    resizeMode: "contain",
  },
});
